'use client';

import { useState, useEffect } from 'react';
import { useSearchParams, useRouter } from 'next/navigation';
import ProductCard from '@/components/ProductCard';

type Product = {
  id: number;
  name: string;
  slug: string;
  code: string;
  base_price: string;
  sale_price: string | null;
  category_name: string;
  status: string;
  is_featured: boolean;
  is_new_arrival: boolean;
  is_bestseller: boolean;
};

type Color = {
  color_id: number;
  color_name: string;
  color_code: string;
};

type Size = {
  size_id: number;
  size_name: string;
};

type Category = {
  category_id: number;
  category_name: string;
};

type Season = {
  value: string;
  label: string;
};

type Filters = {
  price_range: {
    min_price: number;
    max_price: number;
  };
  categories: Category[];
  colors: Color[];
  sizes: Size[];
  seasons: Season[];
};

export default function ProductsPage() {
  const router = useRouter();
  const searchParams = useSearchParams();
  
  // State
  const [products, setProducts] = useState<Product[]>([]);
  const [filters, setFilters] = useState<Filters | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  
  // Filter state
  const [selectedCategories, setSelectedCategories] = useState<number[]>([]);
  const [selectedColors, setSelectedColors] = useState<number[]>([]);
  const [selectedSizes, setSelectedSizes] = useState<number[]>([]);
  const [priceRange, setPriceRange] = useState<[number, number]>([0, 10000]);
  const [selectedSeasons, setSelectedSeasons] = useState<string[]>([]);
  const [searchQuery, setSearchQuery] = useState('');
  
  // Pagination
  const [page, setPage] = useState(1);
  const [hasMore, setHasMore] = useState(true);

  // Load filters on mount
  useEffect(() => {
    loadFilters();
  }, []);

  // Load products when filters change
  useEffect(() => {
    if (filters) {
      loadProducts();
    }
  }, [selectedCategories, selectedColors, selectedSizes, priceRange, selectedSeasons, searchQuery, page]);

  async function loadFilters() {
    try {
      const res = await fetch('/api/catalog/products/filters');
      if (!res.ok) throw new Error('Failed to load filters');
      
      const data = await res.json();
      setFilters(data);
      
      // Set initial price range from data
      if (data.price_range) {
        setPriceRange([
          data.price_range.min_price || 0,
          data.price_range.max_price || 10000
        ]);
      }
    } catch (err: any) {
      console.error('Error loading filters:', err);
      setError(err.message);
    }
  }

  async function loadProducts() {
    try {
      setLoading(true);
      
      // Build query params
      const params = new URLSearchParams();
      
      if (searchQuery) params.append('search', searchQuery);
      if (selectedCategories.length) params.append('category', selectedCategories.join(','));
      if (selectedColors.length) params.append('colors', selectedColors.join(','));
      if (selectedSizes.length) params.append('sizes', selectedSizes.join(','));
      if (selectedSeasons.length) params.append('season', selectedSeasons.join(','));
      if (priceRange[0] > 0) params.append('min_price', priceRange[0].toString());
      if (priceRange[1] < 10000) params.append('max_price', priceRange[1].toString());
      params.append('page', page.toString());
      params.append('page_size', '20');
      
      const res = await fetch(`/api/catalog/products?${params.toString()}`);
      if (!res.ok) throw new Error('Failed to load products');
      
      const data = await res.json();
      
      if (page === 1) {
        setProducts(data.results || data);
      } else {
        setProducts(prev => [...prev, ...(data.results || data)]);
      }
      
      setHasMore(!!data.next);
      setError(null);
    } catch (err: any) {
      console.error('Error loading products:', err);
      setError(err.message);
    } finally {
      setLoading(false);
    }
  }

  function clearFilters() {
    setSelectedCategories([]);
    setSelectedColors([]);
    setSelectedSizes([]);
    setSelectedSeasons([]);
    if (filters?.price_range) {
      setPriceRange([
        filters.price_range.min_price || 0,
        filters.price_range.max_price || 10000
      ]);
    }
    setSearchQuery('');
    setPage(1);
  }

  function toggleCategory(id: number) {
    setSelectedCategories(prev =>
      prev.includes(id) ? prev.filter(c => c !== id) : [...prev, id]
    );
    setPage(1);
  }

  function toggleColor(id: number) {
    setSelectedColors(prev =>
      prev.includes(id) ? prev.filter(c => c !== id) : [...prev, id]
    );
    setPage(1);
  }

  function toggleSize(id: number) {
    setSelectedSizes(prev =>
      prev.includes(id) ? prev.filter(s => s !== id) : [...prev, id]
    );
    setPage(1);
  }

  function toggleSeason(value: string) {
    setSelectedSeasons(prev =>
      prev.includes(value) ? prev.filter(s => s !== value) : [...prev, value]
    );
    setPage(1);
  }

  if (error) {
    return (
      <div className="container mx-auto px-4 py-8">
        <div className="text-center">
          <p className="text-red-600">Ошибка: {error}</p>
          <button
            onClick={() => window.location.reload()}
            className="mt-4 px-4 py-2 bg-black text-white rounded"
          >
            Повторить
          </button>
        </div>
      </div>
    );
  }

  return (
    <div className="container mx-auto px-4 py-8">
      <h1 className="text-3xl font-bold mb-8">Каталог</h1>

      <div className="grid grid-cols-1 lg:grid-cols-4 gap-8">
        {/* Filters Sidebar */}
        <aside className="lg:col-span-1">
          <div className="sticky top-4 space-y-6">
            <div className="flex items-center justify-between">
              <h2 className="text-lg font-semibold">Фильтры</h2>
              <button
                onClick={clearFilters}
                className="text-sm text-gray-600 hover:text-black underline"
              >
                Сбросить
              </button>
            </div>

            {/* Search */}
            <div>
              <input
                type="text"
                placeholder="Поиск..."
                value={searchQuery}
                onChange={(e) => {
                  setSearchQuery(e.target.value);
                  setPage(1);
                }}
                className="w-full px-3 py-2 border rounded-lg"
              />
            </div>

            {/* Categories */}
            {filters?.categories && filters.categories.length > 0 && (
              <div>
                <h3 className="font-medium mb-2">Категории</h3>
                <div className="space-y-2">
                  {filters.categories.map((cat) => (
                    <label key={cat.category_id} className="flex items-center gap-2 cursor-pointer">
                      <input
                        type="checkbox"
                        checked={selectedCategories.includes(cat.category_id)}
                        onChange={() => toggleCategory(cat.category_id)}
                        className="rounded"
                      />
                      <span className="text-sm">{cat.category_name}</span>
                    </label>
                  ))}
                </div>
              </div>
            )}

            {/* Colors */}
            {filters?.colors && filters.colors.length > 0 && (
              <div>
                <h3 className="font-medium mb-2">Цвета</h3>
                <div className="flex flex-wrap gap-2">
                  {filters.colors.map((color) => (
                    <button
                      key={color.color_id}
                      onClick={() => toggleColor(color.color_id)}
                      className={`w-8 h-8 rounded-full border-2 ${
                        selectedColors.includes(color.color_id)
                          ? 'border-black'
                          : 'border-gray-300'
                      }`}
                      style={{ backgroundColor: color.color_code }}
                      title={color.color_name}
                    />
                  ))}
                </div>
              </div>
            )}

            {/* Sizes */}
            {filters?.sizes && filters.sizes.length > 0 && (
              <div>
                <h3 className="font-medium mb-2">Размеры</h3>
                <div className="flex flex-wrap gap-2">
                  {filters.sizes.map((size) => (
                    <button
                      key={size.size_id}
                      onClick={() => toggleSize(size.size_id)}
                      className={`px-3 py-1 border rounded ${
                        selectedSizes.includes(size.size_id)
                          ? 'bg-black text-white border-black'
                          : 'border-gray-300 hover:border-black'
                      }`}
                    >
                      {size.size_name}
                    </button>
                  ))}
                </div>
              </div>
            )}

            {/* Seasons */}
            {filters?.seasons && filters.seasons.length > 0 && (
              <div>
                <h3 className="font-medium mb-2">Сезон</h3>
                <div className="space-y-2">
                  {filters.seasons.map((season) => (
                    <label key={season.value} className="flex items-center gap-2 cursor-pointer">
                      <input
                        type="checkbox"
                        checked={selectedSeasons.includes(season.value)}
                        onChange={() => toggleSeason(season.value)}
                        className="rounded"
                      />
                      <span className="text-sm">{season.label}</span>
                    </label>
                  ))}
                </div>
              </div>
            )}

            {/* Price Range */}
            {filters?.price_range && (
              <div>
                <h3 className="font-medium mb-2">Цена</h3>
                <div className="space-y-2">
                  <div className="flex items-center gap-2">
                    <input
                      type="number"
                      value={priceRange[0]}
                      onChange={(e) => {
                        setPriceRange([Number(e.target.value), priceRange[1]]);
                        setPage(1);
                      }}
                      className="w-full px-2 py-1 border rounded"
                      placeholder="От"
                    />
                    <span>-</span>
                    <input
                      type="number"
                      value={priceRange[1]}
                      onChange={(e) => {
                        setPriceRange([priceRange[0], Number(e.target.value)]);
                        setPage(1);
                      }}
                      className="w-full px-2 py-1 border rounded"
                      placeholder="До"
                    />
                  </div>
                </div>
              </div>
            )}
          </div>
        </aside>

        {/* Products Grid */}
        <main className="lg:col-span-3">
          {loading && page === 1 ? (
            <div className="flex justify-center items-center h-64">
              <div className="text-gray-500">Загрузка...</div>
            </div>
          ) : products.length === 0 ? (
            <div className="text-center py-12">
              <p className="text-gray-500">Товары не найдены</p>
            </div>
          ) : (
            <>
              <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
                {products.map((product) => (
                  <ProductCard
                    key={product.id}
                    href={`/catalog/products/${product.slug}`}
                    imageSrc={`/placeholder-product.jpg`}
                    imageAlt={product.name}
                    title={product.name}
                    sizeLabel={product.category_name}
                    priceFormatted={`${product.base_price} сом`}
                    compareAtFormatted={product.sale_price ? `${product.sale_price} сом` : undefined}
                    className="w-full"
                  />
                ))}
              </div>

              {/* Load More */}
              {hasMore && (
                <div className="mt-8 text-center">
                  <button
                    onClick={() => setPage(p => p + 1)}
                    disabled={loading}
                    className="px-6 py-3 bg-black text-white rounded-lg disabled:opacity-50"
                  >
                    {loading ? 'Загрузка...' : 'Показать ещё'}
                  </button>
                </div>
              )}
            </>
          )}
        </main>
      </div>
    </div>
  );
}